.news-flex{
    display:flex;
    flex-direction:column;
}

.news-flex > .news-card-flex{
    flex:auto;
    margin-left:0.75em;
    margin-right:0.75em;
    margin-bottom: 1em;
    border-radius:10px;
}
.news-card-flex > .news-card-row {
    flex:auto;
    display:flex; 
    flex-direction:row;
    max-height: 224px;  
}
.news-card-flex > .news-card-column {
    flex:auto;
    display:flex; 
    flex-direction:column;
}

.news-card-flex  .news-content {
    flex-grow:3;
    overflow: hidden;
    padding-top: 0.5em;
    & > * {
        padding-right: 0.5em;
        padding-left: 0.5em; 
    }
}

.news-card-flex   .news-img-container {
    flex-grow:0;
    border-radius:10px 0 0 10px;
    max-width: 300px; 
    & > .news-img {
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.05);
        border-radius:10px 0 0 10px;
        object-fit: cover;
        width: 300px;
        height: 224px;
    }
}

.news-card-flex  .article-title {
    font-size: x-large;
    margin-bottom: 0.5em;
    margin-top: 0.25em;
    font-weight: bold;
}


.news-card-flex .article-header {
    font-weight: lighter;
    color: #909090;

    & > * {
        padding-left: 0.5em;
    }
}

.content-snippet {
    margin-bottom: 0.5em;
}

.news-card-flex .source-header {
    font-weight: lighter;
    color: #505050 !important;
    text-decoration: underline;
}

/* change between the small and large view */

.news-card-flex  .alternative {
    display: none;
}

@media(max-width: 500px) {

    .news-card-flex  .main {
        display: none;
    }

    .news-card-flex  .alternative{
        display: block !important;
    }
    
    .alternative>* {
        margin: 0.5em;
    }

    .news-card-flex .news-img-container {
        max-width: revert;
        margin: 0 !important;

        & > .news-img {
            width: 100%; 
            border-radius:10px 10px 0px 0px; 
            margin: 0 !important;
        }
    }
}